$activeStoneColor: rgba(85, 83, 93, 0.97);
$missedStoneColor: lighten($activeStoneColor, 30%);

$retina-break: 2300px;
$media-info: 'only screen and (min-width: $retina-break)';
$font-size: 2em;

@keyframes change-color {
  1% {
    background: #E6E6E7; }
  100% {
    background: $activeStoneColor; } }

@keyframes show-font {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

:global(.controls) {
  position: relative;
  height: 100%;
  width: 100%;

  :global(ul) {
    position: relative;
    list-style: none;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: 0; }

  .button:last-child {
    margin-left: 0; }

  .stone {
    display: flex;
    position: relative; }

  button {
    background: #E6E6E7;
    height: 100%;
    width: 100%;
    margin-right: 5px;
    border-radius: 5px;
    border: none; }

  li:last-child > button {
    margin-right: 0; }

  :global(.active) {
    animation: change-color .2s;
    animation-fill-mode: forwards; }

  :global(.missed) {
    background: $missedStoneColor; }

  .letter {
    @media #{$media-info} {
        font-size: $font-size * 4; }
    color: #FFFFFF;
    font-size: $font-size;
    text-transform: uppercase;
    align-self: center;
    display: inline-block; }

  span {
    opacity: 0;
    animation: show-font .4s;
    animation-fill-mode: forwards;
    animation-delay: .6s; } }



